export const parseLockExceptions = lockExceptionResponse => lockExceptionResponse.map(_ref => {
  let {
    dataSet,
    period,
    organisationUnit
  } = _ref;
  const row = {
    period: period.displayName,
    periodId: period.id,
    dataSet: dataSet.displayName,
    dataSetId: dataSet.id
  };

  if (organisationUnit) {
    row.organisationUnit = organisationUnit.displayName;
    row.organisationUnitId = organisationUnit.id;
  }

  return row;
});